import { Injectable } from '@angular/core';
import { AssetNodeService } from './asset-node.service';
import { NavigatorNode } from '@c8y/ngx-components';

@Injectable()
export class AssetNodeFactory {
  constructor(private service: AssetNodeService) {}

  get(): NavigatorNode {
    if (!this.service.rootNode) {
      this.service.createRootNode();
    }
    return this.service.rootNode;
  }
}
