import { Inject, Injectable, Optional } from '@angular/core';
import { InventoryService, UserService, PagingStrategy } from '@c8y/client';
import {
  AlertService,
  BreadcrumbService,
  ModalService,
  NavigatorNode,
  AppStateService
} from '@c8y/ngx-components';
import { ApiService } from '@c8y/ngx-components/api';
import { empty } from 'rxjs';
import { filter, mergeMap, map, scan } from 'rxjs/operators';
import { AssetNode } from './asset-node';
import { AssetNavigatorConfig, ASSET_NAVIGATOR_CONFIG } from './asset-node-config.model';
import { DynamicGroupNode } from './dynamic-group-node';
import { GroupFragment } from './group-fragment.model';
import { DeviceGroupService } from './group.service';
import { flatMap, includes, reject, map as ldMap } from 'lodash-es';

export interface AssetNodeMo {
  id: string;
  type: string;
}

@Injectable()
export class AssetNodeService {
  rootNode: AssetNode;
  firstUrl = true;
  draggedData: AssetNode;
  protected PAGE_SIZE = 20;

  constructor(
    public inventory: InventoryService,
    public groups: DeviceGroupService,
    public apiService: ApiService,
    public modal: ModalService,
    public alert: AlertService,
    protected breadcrumbService: BreadcrumbService,
    protected user: UserService,
    protected appState: AppStateService,
    @Optional() @Inject(ASSET_NAVIGATOR_CONFIG) public moduleConfig: AssetNavigatorConfig
  ) {
    this.moduleConfig = {
      rootNodePriority: 2000,
      ...(moduleConfig || {})
    };
  }

  createRootNode() {
    this.rootNode = this.createAssetNode({
      root: true,
      priority: this.moduleConfig.rootNodePriority
    });
    return this.rootNode;
  }

  createDynamicGroupNode(config) {
    return new DynamicGroupNode(this, config);
  }

  createAssetNode(config) {
    return new AssetNode(this, config);
  }

  createChildNode(managedObject) {
    const { type } = managedObject;
    const config: Partial<AssetNode> = { mo: managedObject };
    if (type === GroupFragment.dynamicGroupType) {
      return this.createDynamicGroupNode(config);
    }
    return this.createAssetNode(config);
  }

  getRootNodes(): Promise<any> {
    if (this.user.hasRole(this.appState.currentUser.value, 'ROLE_INVENTORY_READ')) {
      const query = this.rootQueryFilter();
      const rootNodeFilter = this.createFilter({
        query,
        pageSize: this.PAGE_SIZE,
        withChildren: false,
        onlyRoots: true
      });
      return this.inventory.list(rootNodeFilter);
    } else {
      const groupFilter = this.createFilter({
        fragmentType: GroupFragment.groupFragmentType,
        withTotalPages: true,
        withChildren: false,
        pageSize: this.PAGE_SIZE,
        onlyRoots: true
      });
      return this.inventory
        .list$(groupFilter, {
          hot: false,
          pagingStrategy: PagingStrategy.NONE,
          realtime: false
        })
        .toPromise();
    }
  }

  getGroupItems(moId: string): Promise<any> {
    return this.inventory.childAssetsList(moId, { withChildren: false, pageSize: this.PAGE_SIZE });
  }

  getDynamicGroupItems(query: string): Promise<any> {
    const dynamicGroupfilter = this.createFilter({ q: query });
    return this.inventory.list(dynamicGroupfilter);
  }

  groupQueryFilter(moId: string) {
    return `$filter=(bygroupid(${moId}))$orderby=name`;
  }

  rootQueryFilter() {
    const { moduleConfig } = this;
    const rootFilter = [`(type eq '${GroupFragment.groupType}')`];
    if (moduleConfig.smartGroups) {
      rootFilter.push(
        `(type eq '${GroupFragment.dynamicGroupType}' and has(${
          GroupFragment.dynamicGroupFragment
        }) and not(has(${GroupFragment.dynamicGroupFragment}.invisible)))`
      );
    }
    return `$filter=(${rootFilter.join(' or ')})$orderby=name`;
  }

  onUpdate({ mo, root }) {
    if (mo.id) {
      return this.apiService
        .hookResponse(
          ({ url, method }) =>
            ['PUT', 'DELETE', 'POST'].includes(method) &&
            RegExp(`((inventory/managedObjects)|(service/smartgroup/smartgroups))/${mo.id}`).test(
              url
            )
        )
        .pipe(
          filter(() => !this.draggedData),
          mergeMap(this.apiService.resolveData),
          filter(response => !response.data.c8y_Dashboard)
        );
    } else if (root) {
      return this.apiService.hookResponse(
        ({ url, method, options }) =>
          RegExp('((inventory/managedObjects)|(service/smartgroup/smartgroups))/?$').test(url) &&
          method === 'POST' &&
          this.isNewManagedObjectRoot(options)
      );
    } else {
      return empty();
    }
  }

  isNewManagedObjectRoot(options: any = {}) {
    const { data } = options;
    let isRootAsset = false;
    if (typeof data === 'object') {
      isRootAsset = !!data[GroupFragment.groupFragmentType];
      if (!isRootAsset && this.moduleConfig.smartGroups) {
        isRootAsset = !!data[GroupFragment.dynamicGroupFragment];
      }
    }
    return isRootAsset;
  }

  /**
   * There could be multiple breadcrumbs for devices,
   * so we set a preferred one on click on a device.
   * @param parents The parent nodes of the device to select the prefered one.
   */
  preferBreadcrumb(parents: NavigatorNode[]) {
    if (parents.length === 1) {
      this.breadcrumbService.selectPreferredByPath(parents[0].path);
    }
  }

  protected createFilter(extraParams: any = {}) {
    const params = {
      currentPage: 1,
      withTotalPages: true,
      pageSize: 10
    };
    return { ...params, ...extraParams };
  }
}
