
import { AssetNode } from './asset-node';
import { AssetNodeService } from './asset-node.service';

export class DynamicGroupNode extends AssetNode {
  constructor(
    protected service: AssetNodeService,
    config = {}
  ) {
    super(service, config);
    this.draggable = false;
    this.droppable = false;
  }

  get hasChildren() {
    return true;
  }

  get query() {
    return this.mo.c8y_DeviceQueryString;
  }

  protected fetch(): any {
    return this.service.getDynamicGroupItems(this.query);
  }
}
